import React, {Component} from "react";
import uuid from "uuid";
import ProductInCart from "./ProductInCart/ProductInCart";

interface ICartCheckoutProps {
	btnText: string;
}
interface ICartCheckoutState {
	numberInCart: number;
}
class CartCheckout extends Component {
	props: ICartCheckoutProps;
	state: ICartCheckoutState;

	constructor(props: ICartCheckoutProps) {
		super(props);
		this.props = props;
		this.state = {
			numberInCart: 2
		};
	}

	removeItemHandler = () => {
		this.setState((prevState: ICartCheckoutState) => {
			return {
				numberInCart: prevState.numberInCart - 1
			};
		});
	};

	render() {
		let itemsInCart = [] as JSX.Element[];
		for (let i = 0; i < this.state.numberInCart; i++) {
			itemsInCart.push(
				<ProductInCart key={uuid.v4()} onRemoveClick={this.removeItemHandler} />
			);
		}

		return (
			<div>
				<button
					type="button"
					className="grid-x button hollow border-none"
					data-toggle="modalCart">
					{this.state.numberInCart > 0 ? (
						<div className="cell grid-x small-up-2">
							<i className="cell fas fa-shopping-cart padding-bottom-1" />
							<div className="cell">
								<span className="badge warning">{this.state.numberInCart}</span>
							</div>
						</div>
					) : (
						<div className="cell grid-x">
							<i className="cell fas fa-shopping-cart padding-bottom-1" />
						</div>
					)}
					<span className="cell">{this.props.btnText}</span>
				</button>
				<div
					className="reveal large"
					id="modalCart"
					data-reveal
					data-close-on-click="true">
					<br />
					{this.state.numberInCart > 0 ? (
						<form data-abide noValidate>
							<strong className="lead">
								Items in your cart: {"{"}
								NumberOfItems
								{"}"}
							</strong>
							<div className="grid-x grid-padding-x">
								<div className="cell medium-8">
									<div className="cell grid-x">
										<p className="cell">
											<i className="fas fa-store" /> {"{"}
											StoreName
											{"}"}
										</p>
										{itemsInCart}
										<div className="cell">
											<br />
											<div className="switch tiny grid-x align-middle">
												<input
													type="checkbox"
													className="switch-input"
													name="giftOrder"
													id="giftOrder"
													aria-label="choose this item as gift"
												/>
												<label htmlFor="giftOrder" className="switch-paddle" />
												<label htmlFor="giftOrder" style={{paddingLeft: "5px"}}>
													This order is a gift
												</label>
											</div>
											<p className="subheader" style={{marginTop: "-15px"}}>
												<small>Prices will not be shown on packing slip</small>
											</p>
										</div>
										<div className="cell">
											<label>
												<textarea
													className="margin-0"
													placeholder="Add a note to {ShopName} (Optional)"
													rows={2}
													defaultValue={""}
												/>
												<small className="subheader display-block padding-bottom-1">
													Use the space above to add special instructions or
													personalization details, if applicable.
												</small>
											</label>
										</div>
									</div>
								</div>
								<div className="cell medium-4 text-right">
									<br className="show-for-small-only" />
									<a href="#">Contact Shop</a>
									<small className="display-block">
										<strong>
											USD {"{"}
											TotalPrice
											{"}"}
										</strong>
									</small>
									<small>
										(USD {"{"}
										Price
										{"}"} each)
									</small>
									<hr className="margin-1" />
									<small>
										Shipping fee: USD {"{"}
										ShippingFee
										{"}"}
									</small>
									<small className="display-block subheader text-right">
										Ready to ship in 1-3 business days from the United Kingdom
									</small>
								</div>
								<div className="cell">
									<hr />
									<div className="grid-x grid-padding-x medium-up-2 padding-top-1">
										<div className="cell grid-x">
											<fieldset className="cell">
												<legend className="lead">Payment method</legend>
												<div className="switch small grid-x align-middle">
													<input
														className="switch-input"
														type="radio"
														name="payment"
														defaultValue="other"
														id="otherPM"
														aria-label="other payments like visa, mastercard or american express"
													/>
													<label className="switch-paddle" htmlFor="otherPM" />
													<label htmlFor="otherPM">
														<span className="h3" style={{paddingLeft: "10px"}}>
															<i className="fab fa-cc-visa" />
															<i className="fab fa-cc-mastercard" />
															<i className="fab fa-cc-amex" />
														</span>
													</label>
												</div>
												<div className="switch small grid-x align-middle">
													<input
														className="switch-input"
														type="radio"
														name="payment"
														defaultValue="paypal"
														id="paypalPM"
														aria-label="pays with paypal"
														defaultChecked
													/>
													<label htmlFor="paypalPM" className="switch-paddle" />
													<label htmlFor="paypalPM">
														<span className="h3" style={{paddingLeft: "10px"}}>
															<i className="fab fa-cc-paypal" />
														</span>
													</label>
												</div>
											</fieldset>
											<div className="cell align-self-bottom text-center">
												<button type="submit" className="button expanded">
													<strong>Proceed to checkout</strong>
												</button>
												<small className="subheader">
													Local taxes included (where applicable). Additional
													duties and taxes <a href="#">may apply</a>.
												</small>
											</div>
										</div>
										<div className="cell">
											<br className="show-for-small-only" />
											<div className="grid-x small-up-2">
												<p className="cell">Item(s) total</p>
												<p className="cell text-right">
													USD {"{"}
													TotalPrice
													{"}"}
												</p>
												<p className="cell">Shipping</p>
												<p className="cell text-right">
													USD {"{"}
													ShippingFee
													{"}"}
												</p>
												<small className="cell padding-bottom-1">
													(To {"{"}
													Location
													{"}"})
												</small>
											</div>
											<hr />
											<div className="grid-x small-up-2 padding-top-1">
												<strong className="cell">
													Total ({"{"}
													NumberOfItems
													{"}"} item)
												</strong>
												<strong className="cell text-right">
													USD {"{"}
													TotalWithFee
													{"}"}
												</strong>
											</div>
										</div>
									</div>
								</div>
							</div>
						</form>
					) : (
						<div className="text-center padding-vertical-1">
							<p className="h4">
								<b>Your cart is empty.</b>
							</p>
							<p>
								<a className="underline" href="#">
									Way to shop new, novel and beautiful products
								</a>
							</p>
						</div>
					)}
					<button
						type="button"
						className="close-button"
						data-close=""
						aria-label="close this modal">
						<span className="lead" aria-hidden="true">
							&times;
						</span>
					</button>
				</div>
			</div>
		);
	}
}
export default CartCheckout;
