import React from "react";
import uuid from "uuid";

const ULMenu = (props: {tabsArr: any}) => {
	return (
		<ul className="menu vertical" key={uuid.v4()}>
			<div className="grid-x small-up-1 medium-up-2 large-up-3">
				{Object.keys(props.tabsArr).map((menuKey) => {
					let subUL = {};
					if (props.tabsArr[menuKey] && props.tabsArr[menuKey].children) {
						const children = props.tabsArr[menuKey].children;
						subUL = (
							<ul className="menu vertical nested magin-0 padding-0">
								{Object.keys(children).map((ulmenuKey) => {
									return (
										<li key={uuid.v4()}>
											<a href={children[ulmenuKey].link}>{ulmenuKey}</a>
										</li>
									);
								})}
							</ul>
						);
					}
					const menuLength = Object.keys(props.tabsArr[menuKey].children)
						.length;
					return (
						<li
							className={
								"cell small-order-" + (menuLength > 6 ? 6 : menuLength)
							}
							key={uuid.v4()}>
							<a href={props.tabsArr[menuKey].link}>{menuKey}</a>
							{subUL}
						</li>
					);
				})}
			</div>
		</ul>
	);
};

export default ULMenu;
