import React, {Component} from "react";
import {BrowserRouter, Route, Switch, Router} from "react-router-dom";

import "./App.css";
import OffCanvasMenu from "./OffCanvasMenu/OffCanvasMenu";
import PageFooter from "../PageFooter/PageFooter";
import PageHeader from "../PageHeader/PageHeader";
import HomePage from "../HomePage/HomePage";
import SellPage from "../SellPage/SellPage";
import ProductPage from "../ProductPage/ProductPage";
import ShopPage from "../ShopPage/ShopPage";
import UserPage from "../UserPage/UserPage";
import CategoryPage from "../CategoryPage/CategoryPage";
import NoMatchPage from "../NoMatchPage/NoMatchPage";

interface IAppState {}
class App extends Component {
	state: IAppState;

	constructor(props: any) {
		super(props);
		this.state = {};
	}

	componentDidMount = () => {
		const reviewTogglers = document.querySelectorAll(
			".review-toggler"
		) as NodeListOf<HTMLAnchorElement>;
		reviewTogglers.forEach((a) => {
			a.addEventListener("click", (e) => {
				const text = a.text.toLocaleLowerCase().trim();
				if (text === "read more +") {
					a.text = "Read less -";
				} else if (text === "read less -") {
					a.text = "Read more +";
				}
			});
		});
	};

	initializeFoundation = () => {
		($(document) as any).foundation();
	};

	render() {
		return (
			<BrowserRouter>
				<div className="off-canvas-wrapper">
					<div
						className="off-canvas position-left"
						data-off-canvas
						data-content-scroll="false"
						data-transition="overlap"
						id="menuCanvas"
						style={{background: "white"}}>
						<OffCanvasMenu />
					</div>
					<div className="off-canvas-content" data-off-canvas-content>
						<div className="grid-container full">
							<PageHeader />
							{/* <Switch>
								<Route exact path="/" component={HomePage} />
								<Route path="/sell" component={SellPage} />
								<Route path="/product/:id" component={ProductPage} />
								<Route path="/shop/:id" component={ShopPage} />
								<Route path="/user/:id" component={UserPage} />
								<Route path="/category/:id" component={CategoryPage} />
								<Route component={NoMatchPage} />
							</Switch> */}
							<PageFooter />
						</div>
					</div>
				</div>
			</BrowserRouter>
		);
	}
}

export default App;
