import React, {Component} from "react";

interface ISignInModalProps {
	className: string;
	text: string;
	authHandler: (logged: boolean, emailNew?: string | undefined) => void;
}
interface ISignInModalState {
	emailSignInInput: string;
	passwordSignInInput: string;
}
class SignInModal extends Component {
	props: ISignInModalProps;
	state: ISignInModalState;

	constructor(props: ISignInModalProps) {
		super(props);
		this.props = props;
		this.state = {
			emailSignInInput: "",
			passwordSignInInput: ""
		};
	}

	componentDidMount = () => {
		$(document).on("submit", (e) => {
			e.preventDefault();
			if ((e.target as any).id === "signinForm") {
				this.signinSubmit(e.target as any);
			}
		});
	};

	inputHandler = (name: string, value: string) => {
		this.setState((prevState: ISignInModalState) => {
			return {
				[name + "SignInInput"]: value
			};
		});
	};

	signinSubmit = (form: HTMLFormElement) => {
		const formData = new FormData(form);
		let postData = {};
		formData.forEach((value, key) => {
			postData[key] = value;
		});
		fetch("https://ecommerce.example.com:5050/auth/login", {
			method: "POST",
			headers: {
				accept: "application/json",
				"content-type": "application/json"
			},
			credentials: "include",
			body: JSON.stringify(postData)
		})
			.then((res) => {
				return res.json();
			})
			.then((data) => {
				(document.querySelector("#signInClose") as HTMLButtonElement).click();
				if (!data.isLoggedIn) {
					alert(data.error);
				} else {
					this.props.authHandler(true, data.user.displayName);
				}
			});
	};

	fetchAuth = (type: string) => {
		const fetchE = (e: React.MouseEvent<HTMLButtonElement>) => {
			fetch(`https://ecommerce.example.com:5050/auth/${type}`, {
				method: "GET",
				headers: {
					accept: "application/json"
				},
				mode: "no-cors",
				credentials: "include"
			})
				.then((res) => {
					return res.json();
				})
				.then((data) => {
					(document.querySelector("#signInClose") as HTMLButtonElement).click();
					if (!data.isLoggedIn) {
						alert(data.error);
					} else {
						this.props.authHandler(true, data.user.displayName);
					}
				});
		};
		return fetchE;
	};

	render() {
		return (
			<div>
				<button
					id="signinBtn"
					type="button"
					className={this.props.className}
					data-toggle="modalSignup">
					{this.props.text}
				</button>
				<div
					className="reveal small"
					id="modalSignup"
					data-reveal
					data-close-on-click="true">
					<strong className="lead">Sign in to continue</strong>
					<form id="signinForm" data-abide noValidate>
						<label>
							Email address{" "}
							<small>
								<em className="subheader">(required)</em>
							</small>
							<input
								type="email"
								aria-errormessage="emailErr"
								pattern="email"
								name="email"
								required
								value={this.state.emailSignInInput}
								onChange={(e) => {
									this.inputHandler(e.target.name, e.target.value);
								}}
							/>
							<span className="form-error" id="emailErr">
								Please fill in your email address.
							</span>
						</label>
						<label>
							Password{" "}
							<small>
								<em className="subheader">(required)</em>
							</small>
							<input
								type="password"
								name="password"
								aria-errormessage="passErr"
								required
								value={this.state.passwordSignInInput}
								onChange={(e) => {
									this.inputHandler(e.target.name, e.target.value);
								}}
							/>
							<span className="form-error" id="passErr">
								Please fill in your password.
							</span>
						</label>
						<div className="grid-x small-up-2">
							<div className="cell switch tiny grid-x">
								<input
									className="switch-input"
									type="checkbox"
									name="staySigned"
									id="staySigned"
									aria-label="stay signed-in"
								/>
								<label className="switch-paddle" htmlFor="staySigned" />
								<label htmlFor="staySigned" style={{paddingLeft: "5px"}}>
									Stay signed in
								</label>
							</div>
							<a href="#" className="cell text-right">
								<small>Forgot your password?</small>
							</a>
						</div>
						<button
							type="submit"
							className="button secondary expanded margin-top-1">
							<span className="h4">Sign in</span>
						</button>
					</form>
					<div className="grid-x text-center">
						<a className="cell margin-top-1" href="#">
							Trouble signing in?
						</a>
						<div className="cell">
							<hr className="margin-vertical-1" />
							<p className="h5">OR</p>
						</div>
					</div>
					<button
						className="button hollow expanded"
						onClick={this.fetchAuth("google")}>
						<i className="fab fa-google" />{" "}
						<strong>Continue with Google</strong>
					</button>
					<button
						id="facebookAuthBtn"
						className="button hollow expanded"
						onClick={this.fetchAuth("facebook")}>
						<i className="fab fa-facebook" />{" "}
						<strong>Continue with Facebook</strong>
					</button>
					<p className="margin-top-1">
						<small>
							By clicking "Continue with Google" or "Continue with Facebook",
							you agree to Vsy's Terms of Use and Privacy Policy. Vsy may send
							you communications; You may change your preferences in your
							account settings. We'll never post without your permission.
						</small>
					</p>
					<button
						id="signInClose"
						type="button"
						className="close-button"
						data-close=""
						aria-label="close this modal">
						<span className="lead" aria-hidden="true">
							&times;
						</span>
					</button>
				</div>
			</div>
		);
	}
}

export default SignInModal;
