import React from "react";
import {Link} from "react-router-dom";

const UserInfo = (props: {
	className: string;
	text: string;
	authHandler: (logged: boolean, displayNameNew?: string | undefined) => void;
}) => {
	const loggoutHandler = () => {
		fetch("https://ecommerce.example.com:5050/auth/logout", {
			method: "GET",
			headers: {
				accept: "application.json"
			},
			credentials: "include"
		})
			.then((res) => {
				return res.json();
			})
			.then((data) => {
				(document.querySelector(
					"#userDropDownBtn"
				) as HTMLButtonElement).click();
				if (data.isLoggedOut) {
					props.authHandler(false);
				} else {
					alert(data.error);
				}
			});
	};

	return (
		<div>
			<button
				id="userDropDownBtn"
				type="button"
				className={props.className}
				data-toggle="userInfoDropdown">
				<span className="p">
					<i className="fas fa-user-circle" />{" "}
					<i className="fas fa-caret-down" />
				</span>
				<small className="p padding-top-1 show-for-medium">{props.text}</small>
			</button>
			<div
				className="dropdown-pane padding-0"
				id="userInfoDropdown"
				data-dropdown
				data-auto-focus="true"
				data-position="bottom"
				data-alignment="right"
				data-h-offset="-90">
				<Link
					className="grid-x align-middle padding-vertical-1"
					to="/user/test">
					<div className="cell small-3">
						<b>
							<i className="fas fa-user-circle" style={{fontSize: "3em"}} />
						</b>
					</div>
					<div className="cell auto text-left">
						<span className="p">
							<b>Antonio</b>
						</span>
						<button type="button" className="button hollow">
							View profile <i className="fas fa-angle-right" />
						</button>
					</div>
				</Link>
				<hr />
				<div className="padding-1 text-left">
					<p>
						<a href="#">
							<i className="far fa-heart" /> Favorites
						</a>
					</p>
					<p>
						<a href="#">
							<i className="far fa-comments" /> Conversations
						</a>
					</p>
					<p>
						<a href="#">Purchases and reviews</a>
					</p>
					<p>
						<a href="#">Account settings</a>
					</p>
					<span>
						<a href="#">Your teams</a>
					</span>
				</div>
				<hr />
				<div className="padding-1 text-left">
					<a
						id="logoutBtn"
						onClick={(e) => {
							loggoutHandler();
						}}>
						Sign out
					</a>
				</div>
			</div>
		</div>
	);
};

export default UserInfo;
