import React, {Component} from "react";
import {Link} from "react-router-dom";
import CartCheckout from "./CartCheckout/CartCheckout";
import RegisterModal from "./NotLoggedIn/RegisterModal/RegisterModal";
import SigninModal from "./NotLoggedIn/SignInModal/SignInModal";
import SearchBar from "../SearchBar/SearchBar";
import MenuNav from "./MenuNav/MenuNav";
import NotificationBell from "./LoggedIn/NotificationBell/NotificationBell";
import UserInfo from "./LoggedIn/UserInfo/UserInfo";

import menu from "../menu.json";
import logo from "./logo.svg";
import "./PageHeader.css";

interface IPageHeaderState {
	isLoggedin: boolean;
	displayName: string;
}
class PageHeader extends Component {
	state: IPageHeaderState;

	constructor(props: any) {
		super(props);
		this.state = {
			isLoggedin: false,
			displayName: ""
		};
	}

	componentDidMount = () => {
		this.authCheckHandler();
	};

	authCheckHandler = () => {
		fetch("https://ecommerce.example.com:5050/auth/check", {
			method: "GET",
			headers: {
				accept: "application/json"
			},
			credentials: "include"
		})
			.then((res) => {
				return res.json();
			})
			.then((data) => {
				if (data.isLoggedIn) {
					this.authHandler(true, data.displayName);
				}
			});
	};

	authHandler = (logged: boolean, displayNameNew?: string) => {
		this.setState((prevState: IPageHeaderState) => {
			return {
				isLoggedin: logged,
				displayName: displayNameNew ? displayNameNew : ""
			};
		});
	};

	render() {
		return (
			<div className="padding-top-1 main-background">
				<div className="grid-x">
					<div className="cell medium-6 grid-x align-middle">
						<Link to="/" className="cell small-3">
							<img src={logo} className="app-logo" alt="logo" />
						</Link>
						<div className="cell medium-9 show-for-large">
							<SearchBar />
						</div>
						<div className="cell small-9 show-for-small hide-for-large">
							<a className="underline" data-toggle="menuCanvas">
								<i className="lead fas fa-bars padding-horizontal-1" />
								Browse
							</a>
						</div>
					</div>

					<div
						className="cell medium-6 grid-x small-up-4 large-up-6 align-middle text-center padding-bottom-1"
						id="loggedDiv">
						<div className="cell large-offset-4">
							<Link
								to="/sell"
								className={
									!this.state.isLoggedin
										? "button a underline text-nowrap"
										: "button a underline text-nowrap hide"
								}>
								Sell on Vsy
							</Link>
							<NotificationBell
								className={
									this.state.isLoggedin
										? "button hollow border-none text-center padding-0"
										: "button hollow border-none text-center padding-0 hide"
								}
								text="Notification"
							/>
						</div>
						<div className="cell">
							<RegisterModal
								className={
									!this.state.isLoggedin
										? "button a underline"
										: "button a underline hide"
								}
								text="Register"
								authHandler={this.authHandler}
							/>
							<a
								className={
									this.state.isLoggedin
										? "button hollow border-none text-center padding-0"
										: "button hollow border-none text-center padding-0 hide"
								}
								onClick={(e) => {
									this.authCheckHandler();
								}}>
								<p className="margin-0" style={{lineHeight: 0}}>
									<i className="fas fa-store" />
								</p>
								<small className="p padding-top-1 show-for-medium">
									Shop Manager
								</small>
							</a>
						</div>
						<div className="cell">
							<SigninModal
								className={
									!this.state.isLoggedin
										? "button signin"
										: "button signin hide"
								}
								text="Sign in"
								authHandler={this.authHandler}
							/>
							<UserInfo
								className={
									this.state.isLoggedin
										? "button hollow border-none text-center padding-0"
										: "button hollow border-none text-center padding-0 hide"
								}
								text={this.state.displayName}
								authHandler={this.authHandler}
							/>
						</div>
						<div className="cell grid-x align-center">
							<CartCheckout btnText="Cart" />
						</div>
					</div>
				</div>
				<div className="show-for-large">
					<MenuNav menu={menu} />
				</div>
				<hr className="hide-for-large" />
				<div className="hide-for-large padding-1">
					<SearchBar />
				</div>
				<hr className="hide-for-large" />
			</div>
		);
	}
}

export default PageHeader;
