import React, {Component} from "react";
import uuid from "uuid";

const SearchBar = () => {
	const searchBarID = uuid.v4();
	return (
		<form action="/search" method="POST" data-abide noValidate>
			<div className="input-group margin-0">
				<input
					type="search"
					id={searchBarID}
					name="searchBar"
					className="input-group-field"
					required
				/>
				<span className="input-group-button">
					<button type="submit" className="button">
						Search
					</button>
				</span>
			</div>
			<span
				className="form-error position-absolute"
				data-form-error-for={searchBarID}
				style={{marginTop: "-1px"}}>
				Please type in something to search for...
			</span>
		</form>
	);
};

export default SearchBar;
