import React, {Component} from "react";
import Tabs from "./Tabs/Tabs";
import TabsContent from "./TabsContent/TabsContent";
import ULMenu from "./ULMenu/ULMenu";
import "./MenuNav.css";

interface IMenuNavProps {
	menu: any;
}
interface IMenuNavState {
	currentTab: string;
}
class MenuNav extends Component {
	props: IMenuNavProps;
	state: IMenuNavState;

	constructor(props: IMenuNavProps) {
		super(props);
		this.props = props;
		this.state = {
			currentTab: ""
		};
	}

	render() {
		const menu = this.props.menu;
		return (
			<div id="menuBarContainer">
				<Tabs
					className="tabs grid-x text-center"
					tabsArr={menu}
					id="menuTabs"
					uppercaseText={false}
				/>
				<div className="tabs-content" data-tabs-content="menuTabs">
					{Object.keys(menu).map((menuKey, idx) => {
						const children = menu[menuKey].children;
						if (!children || Object.keys(children).length === 0) {
							return <div key={idx} />;
						}
						const shallowMenu = Object.keys(children).find((childKey) => {
							return Object.keys(children[childKey].children).length === 0;
						});
						if (!shallowMenu) {
							return <TabsContent key={menuKey} tabsArr={menu[menuKey]} />;
						} else {
							return (
								<div
									className="tabs-panel"
									id={menu[menuKey].link}
									key={menuKey}>
									<ULMenu tabsArr={menu[menuKey].children} />
								</div>
							);
						}
					})}
				</div>
			</div>
		);
	}
}

export default MenuNav;
