import React, {Component} from "react";

interface IRegisterModalProps {
	className: string;
	text: string;
	authHandler: (logged: boolean, emailNew?: string | undefined) => void;
}
interface IRegisterModalState {
	emailRegInput: string;
	firstNameRegInput: string;
	passwordRegInput: string;
}
class RegisterModal extends Component {
	props: IRegisterModalProps;
	state: IRegisterModalState;

	constructor(props: IRegisterModalProps) {
		super(props);
		this.props = props;
		this.state = {
			emailRegInput: "",
			firstNameRegInput: "",
			passwordRegInput: ""
		};
	}

	componentDidMount = () => {
		$(document).on("submit", (e) => {
			e.preventDefault();
			if ((e.target as any).id === "registerForm") {
				this.registerSubmit(e.target as any);
			}
		});
	};

	inputHandler = (name: string, value: string) => {
		this.setState((prevState: IRegisterModalState) => {
			return {
				[name + "RegInput"]: value
			};
		});
	};

	registerSubmit = (form: HTMLFormElement) => {
		const formData = new FormData(form);
		let postData = {};
		formData.forEach((value, key) => {
			postData[key] = value;
		});
		fetch("https://ecommerce.example.com:5050/auth/register", {
			method: "POST",
			headers: {
				accept: "application/json",
				"content-type": "application/json"
			},
			body: JSON.stringify(postData)
		})
			.then((res) => {
				return res.json();
			})
			.then((data) => {
				(document.querySelector("#registerClose") as HTMLButtonElement).click();
				if (!data.created) {
					alert(
						data.error ||
							"Something wrong while registing new account! Please contact the admin"
					);
				} else {
					alert(
						"Account has been created! Please sign in using your new account!"
					);
					this.setState((prevState: IRegisterModalState) => {
						return {
							emailRegInput: "",
							firstNameRegInput: "",
							passwordRegInput: ""
						};
					});
					(document.querySelector("#signinBtn") as HTMLButtonElement).click();
				}
			});
	};

	fetchAuth = (type: string) => {
		const fetchE = (e: React.MouseEvent<HTMLButtonElement>) => {
			fetch(`https://ecommerce.example.com:5050/auth/${type}`, {
				method: "GET",
				headers: {
					accept: "application/json"
				},
				mode: "no-cors",
				credentials: "include"
			})
				.then((res) => {
					return res.json();
				})
				.then((data) => {
					(document.querySelector("#signInClose") as HTMLButtonElement).click();
					if (!data.isLoggedIn) {
						alert(data.error);
					} else {
						this.props.authHandler(true, data.user.displayName);
					}
				});
		};
		return fetchE;
	};

	render() {
		return (
			<div>
				<div>
					<a
						type="button"
						className={this.props.className}
						data-toggle="modalReg">
						{this.props.text}
					</a>
					<div
						className="reveal small"
						id="modalReg"
						data-reveal
						data-close-on-click="true">
						<strong className="lead">Create your account</strong>
						<p>
							<small>Registration is easy.</small>
						</p>
						<form data-abide noValidate id="registerForm">
							<label>
								Email address{" "}
								<small>
									<em className="subheader">(required)</em>
								</small>
								<input
									type="email"
									aria-errormessage="emailRegErr"
									pattern="email"
									name="email"
									required
									value={this.state.emailRegInput}
									onChange={(e) => {
										this.inputHandler(e.target.name, e.target.value);
									}}
								/>
								<span className="form-error" id="emailRegErr">
									Please fill in your email address.
								</span>
							</label>
							<label>
								First name{" "}
								<small>
									<em className="subheader">(required)</em>
								</small>
								<input
									type="text"
									aria-errormessage="nameRegErr"
									name="firstName"
									required
									value={this.state.firstNameRegInput}
									onChange={(e) => {
										this.inputHandler(e.target.name, e.target.value);
									}}
								/>
								<span className="form-error" id="nameRegErr">
									Please fill in your first name.
								</span>
							</label>
							<label>
								Password{" "}
								<small>
									<em className="subheader">(required)</em>
								</small>
								<input
									type="password"
									name="password"
									aria-errormessage="passRegErr"
									required
									value={this.state.passwordRegInput}
									onChange={(e) => {
										this.inputHandler(e.target.name, e.target.value);
									}}
								/>
								<span className="form-error" id="passRegErr">
									Please fill in your password.
								</span>
							</label>
							<button type="submit" className="button secondary expanded">
								<span className="h4">Register</span>
							</button>
						</form>
						<div className="grid-x text-center">
							<p className="cell h5 margin-0 padding-vertical-1">OR</p>
						</div>
						<button
							className="button hollow expanded"
							onClick={this.fetchAuth("google")}>
							<i className="fab fa-google" />{" "}
							<strong>Continue with Google</strong>
						</button>
						<button
							className="button hollow expanded"
							onClick={this.fetchAuth("facebook")}>
							<i className="fab fa-facebook" />{" "}
							<strong>Continue with Facebook</strong>
						</button>
						<p className="margin-top-1">
							<small>
								By clicking "Continue with Google" or "Continue with Facebook",
								you agree to Vsy's Terms of Use and Privacy Policy. Vsy may send
								you communications; you may change your preferences in your
								account settings. We'll never post without your permission.
							</small>
						</p>
						<button
							id="registerClose"
							type="button"
							className="close-button"
							data-close=""
							aria-label="close this modal">
							<span className="lead" aria-hidden="true">
								&times;
							</span>
						</button>
					</div>
				</div>
			</div>
		);
	}
}

export default RegisterModal;
