import React from "react";

import Tabs from "../Tabs/Tabs";
import ULMenu from "../ULMenu/ULMenu";
import uuid from "uuid";

const TabsContent = (props: {tabsArr: any}) => {
	const children = props.tabsArr.children;
	const submenuID = "submenuTab-" + uuid.v4();
	return (
		<div className="tabs-panel" id={props.tabsArr.link} key={uuid.v4()}>
			<div className="grid-x">
				<div className="cell small-3">
					<Tabs
						className="tabs vertical border-none"
						tabsArr={children}
						id={submenuID}
						uppercaseText={true}
					/>
				</div>
				<div className="cell small-9">
					<div
						className="tabs-content height-100 border-none"
						data-tabs-content={submenuID}>
						{Object.keys(children).map((submenuKey) => {
							const children2 = children[submenuKey].children;
							return (
								<div
									className="tabs-panel"
									key={uuid.v4()}
									id={children[submenuKey].link}>
									<ULMenu tabsArr={children2} />
								</div>
							);
						})}
					</div>
				</div>
			</div>
		</div>
	);
};

export default TabsContent;
