import React, {Component} from "react";

interface ITabsProps {
	className: string;
	tabsArr: any[];
	id: string;
	children?: any;
	uppercaseText: boolean;
}
class Tabs extends Component {
	props: ITabsProps;

	constructor(props: ITabsProps) {
		super(props);
		this.props = props;
	}

	componentDidMount = () => {
		function tabsToggle(elem: HTMLElement, toggle: boolean) {
			$(elem).attr("aria-selected", toggle.toString());
			if (elem.parentElement) {
				toggle
					? $(elem.parentElement).addClass("is-active")
					: $(elem.parentElement).removeClass("is-active");
			}
			const panelID = $(elem).attr("href");
			if (panelID) {
				toggle
					? $(panelID).addClass("is-active")
					: $(panelID).removeClass("is-active");
			}
		}
		let prevA: any;
		const menuTabs = document.querySelectorAll(
			`ul#${this.props.id} li a`
		) as NodeListOf<HTMLAnchorElement>;

		menuTabs.forEach((a) => {
			a.addEventListener("mouseover", (e) => {
				if (!prevA || prevA.id !== a.id) {
					menuTabs.forEach((a1) => {
						tabsToggle(a1, false);
					});
					prevA = a;
					tabsToggle(a, true);
				}
			});
			a.addEventListener("click", () => {
				const aJQ = $(a).attr("href");
				if (aJQ) {
					window.location.replace(aJQ.replace("#", "category/"));
				}
			});
		});

		const body = document.querySelector("body") as HTMLBodyElement;
		body.addEventListener("mousemove", (e) => {
			const menuBar = $("div#menuBarContainer");
			const menuBarLeft = menuBar.position().left;
			const menuBarTop = menuBar.position().top;
			const menuBarOuterWidth = menuBar.outerWidth() || 0;
			const menuBarOuterHeight = menuBar.outerHeight() || 0;
			if (
				e.pageX < menuBarLeft ||
				e.pageX > menuBarLeft + menuBarOuterWidth ||
				e.pageY < menuBarTop ||
				e.pageY > menuBarTop + menuBarOuterHeight
			) {
				menuTabs.forEach((a1) => {
					tabsToggle(a1, false);
				});
				const selectedA = document.querySelector(
					`ul#${this.props.id} li a[aria-selected="true"]`
				) as HTMLElement;
				if (selectedA) {
					tabsToggle(selectedA, true);
				}
				prevA = null;
			}
		});
	};

	render() {
		const tabsArr = this.props.tabsArr;
		return (
			<ul
				className={
					this.props.className + ` small-up-${Object.keys(tabsArr).length}`
				}
				data-tabs
				data-active-collapse="true"
				id={this.props.id}>
				{Object.keys(tabsArr).map((tabsKey) => {
					return (
						<li className="cell tabs-title" key={tabsArr[tabsKey].link}>
							<a className="main-background" href={"#" + tabsArr[tabsKey].link}>
								<span>
									{this.props.uppercaseText
										? tabsKey.toLocaleUpperCase()
										: tabsKey}
								</span>
							</a>
						</li>
					);
				})}
			</ul>
		);
	}
}

export default Tabs;
